
html, body, div, section, main, nav, figure, p, a, h1, h2, h3, h4, h5, h6, ul, ol, li {
  box-sizing: border-box;
  margin: 0;
}
article, aside, details, figcaption, figure, footer, header, main, nav, section, video, summary {
  display: block;
}
a:focus {
  outline: none;
}
a:active,
a:hover {
  outline: 0;
}
img {
  border: 0;
  width: 100%;
  height: 100%;
  vertical-align: middle;
}
svg:not(:root) {
  overflow: hidden;
}
ol, ul {
  list-style: none;
  padding: 0;
}
html {
  font: 100%/1.4 "OpenSansRegular", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: normal;
  font-smooth: always;
  text-rendering: auto;
  color: #1a1a1a;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  height: 100%;
  cursor: default;
  background-color: white;
}
p a:hover, p a:focus, article a:hover, article a:focus {
  color: #6774e4;
}
.highlighter {
  display: inline-block;
  position: relative;
}
.highlighter::after {
  content: "";
  display: block;
  position: absolute;
  top: 80%;
  background-color: #6774e4;
  height: 2px;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  transition: -webkit-transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
a:hover .highlighter::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.button {
  display: inline-block;
  height: 40px;
  margin-right: 2rem;
}
.button .highlighter {
  font: 1.125rem/40px "OpenSansRegular", "Helvetica Neue", Helvetica, sans-serif;
  color: #6774e4;
}
p .highlighter::before, article .highlighter::before {
  content: "";
  display: block;
  position: absolute;
  background-color: rgba(194, 194, 214, 0.5);
  height: 2px;
  width: 100%;
}
p .highlighter::after, p .highlighter::before,
article .highlighter::after, article .highlighter::before {
  top: 90%;
}
.button svg {
  stroke: #6774e4;
  margin-left: 6px;
  -webkit-transform: translate(0, 2px);
          transform: translate(0, 2px);
}
.headroom {
  will-change: transform;
  box-shadow: 1px 2px 20px rgba(0, 0, 0, 0.35);
  transition: -webkit-transform 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: transform 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: transform 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.headroom--unpinned {
  -webkit-transform: translate3d(0, -65px, 0);
          transform: translate3d(0, -65px, 0);
}
.headroom--pinned {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.expanded.headroom--top {
  box-shadow: none;
}
