}
.gallery {
  width: 100%;
  height: 100%;
  z-index: 1;
}
.gallery::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    width: 75%;
    height: 100%;
    margin-left: 25%;
    z-index: -1;
}
.static {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.item {
  margin: 5vh 5vw 10vh 0;
  overflow: hidden;
  border-radius: 2px;
